// BACKGROUND IMAGE
.bg-img-fix {
    background-attachment: unset;
	background-size: cover;
}
.full-img{
	width:100%;
}

.sticky-top{
	top:60px;
}
.page-title {
    padding : 65px 0px 45px;
    position: relative;
    background         : $light;
   
    background-size    : cover;
    background-position: center;

    p {
        font-size     : 20px;
        font-weight  : 500;
        margin-bottom : 5px;
        color         : #7272A8;
        text-transform: capitalize;
		@include respond('phone'){
			font-size:16px;
		}
    }

    h3 {
        font-size     : 40px;
        line-height   : 1.2;
        font-weight  : 600;
        text-transform: capitalize;
		@include respond('phone'){
			font-size: 28px;
		}
    }
}
section{
    position: relative;
	z-index:1;
	&:after{
		content: attr(data-content);
		position: absolute;
		top: -28px;
		font-size: 200px;
		font-weight: 700;
		color: transparent;
		line-height: 1;
		-webkit-text-stroke: 1px #000;
		z-index: -1;	
		font-family:$font-family-title;
		opacity:0.1;
		left: 0;
		
		@include respond('laptop'){
			font-size:140px;
		    top: -20px;	
		}	
		@include respond('tab-port'){
			font-size: 80px;
			top: -10px;	
		}	
		@include respond('phone'){		
			font-size: 50px;
			top: -5px;	
		}
	}
}
