// site button 
.dlab-bnr-inr{
	background:$light;
    height: 450px;
    background-image: url(../images/banner.png);
    background-size: 50%;
    background-position: right bottom;
    background-repeat: no-repeat;
	position: relative;
    overflow: hidden;
	@include respond('tab-port'){
		height: 300px;
	}
	&:before{
		content: "";
		width: 200px;
		height: 200px;
		background: $primary;
		position: absolute;
		bottom: 0;
		left: 0;
		transform: rotate(45deg) translate(0px, 140px);
		-moz-transform: rotate(45deg) translate(0px, 140px);
		-webkit-transform: rotate(45deg) translate(0px, 140px);
		-ms-transform: rotate(45deg) translate(0px, 140px);
		-o-transform: rotate(45deg) translate(0px, 140px);
		@include respond('tab-port'){
			width: 150px;
			height: 150px;
		}
	}
	.container{
		display: table;
		height: 100%;
		
	}
	.dlab-bnr-inr-entry{
	    height: 450px;
		vertical-align: middle;
		display: table-cell;
		padding-top: 100px;	
		@include respond('tab-port'){
			height: 300px;
		}		
		@include respond('phone-land'){
			padding-top: 60px;	
			text-align:center;
			.breadcrumb-row{
				display:inline-block;
			}
		}	
	}
	h1 {
		font-weight: 600;
		font-size: 50px;
		margin-bottom: 5px;
		color: #000;
		text-transform: uppercase;
		@include respond('tab-port'){
			font-size: 35px;
		}	
	}
}
.breadcrumb-row{
	ul {
		background:transparent;
		padding: 0;
		
		li {
			padding: 0;
			margin-right: 3px;
			color: #fff;
			display: inline-block;
			font-size: 18px;
			font-weight: 400;
			color:#777;
			
			a{
				color:$primary;
			}
		}
	}
}








